.contact-us{height:100%;background-color:#f2f2f2;color:#898989}.contact-us p{margin:0}.contact-us .app-logo{width:100%;height:14.45rem;text-align:center}.contact-us .app-logo img{margin:3rem 0;width:10.5rem;height:8.45rem}.contact-us .item{width:100%;line-height:2.4rem;background-color:#fff;border-top:1px solid #f0eff5;font-size:.7rem;position:relative}.contact-us .item span:nth-child(1){color:#000;text-align:left;margin-left:.6rem}.contact-us .item span:nth-child(2){text-align:right;position:absolute;right:.6rem}.contact-us .copyright{width:100%;text-align:center;font-size:.6rem;position:absolute;bottom:2.5rem}


