.my-course{background:#fff}.my-course p{margin:0}.my-course .my-course-drfts{width:5rem;color:#fff;position:absolute;top:0;right:0}.my-course .time-box{padding:3rem 0 1.5rem;background:#00b0f0;font-size:.7rem;text-align:center;color:#fff}.my-course .time-box .time{font-size:2.25rem}.my-course .changtou-card{margin:.5rem .75rem;padding:0 1.25rem;position:relative;font-size:.75rem;line-height:2.25rem;color:#fff;background:#33d6ad;border-radius:5px}.my-course .changtou-card .left-round,.my-course .changtou-card .right-round{position:absolute;display:inline-block;height:2.25rem;line-height:2.25rem}.my-course .changtou-card .left-round{left:-0.5rem}.my-course .changtou-card .right-round{right:-0.5rem}.my-course .changtou-card .left-round:before,.my-course .changtou-card .right-round:before{position:relative;display:inline-block;vertical-align:text-top;width:1rem;height:1rem;border-radius:50%;background:#fff;content:''}.my-course .changtou-card .help-icon{display:inline-block;line-height:2.25rem;position:absolute;right:1.25rem}.my-course .changtou-card .help-icon:before{font-family:'myicon';content:'\e91a';color:#fff;font-size:1rem !important;vertical-align:middle}.my-course .course-list{background:#fff;overflow:hidden;border-bottom:1px solid #f0eff5}.my-course .course-list-img{width:7.5rem;height:3.5rem;margin:.5rem .375rem .5rem .75rem;vertical-align:top}.my-course .course-list-info{position:relative;display:inline-block;margin:.5rem .75rem .5rem .375rem;width:8.5rem;vertical-align:top;line-height:1rem}.my-course .course-list-title{color:#000;font-size:.8rem;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.my-course .course-list-subtitle{color:#898989;font-size:.7rem;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.my-course .course-list-state{padding-top:.5rem;color:#00b0f0;font-size:.6rem}.my-course .course-list-state:before{margin-right:.25rem;font-family:'myicon';content:'\e906';color:#00b0f0;font-size:.7rem !important;vertical-align:bottom}


