.course-navigator p{margin:0}.course-navigator .area-label{text-align:left;height:2.2rem;line-height:2.2rem;width:100%;padding:0 4%}.course-navigator .area-label span{display:inline-block;vertical-align:middle}.course-navigator .area-label .color-span{width:2%;height:1rem;background-color:#00b0f0}.course-navigator .area-label .title{padding-left:2%;font-size:.8rem;width:68%}.course-navigator .area-label .subtitle{right:4%;padding:0;font-size:.7rem;color:#aaa;text-align:right}.course-navigator .area-label .subtitle:after{content:'';display:inline-block;position:relative;left:5%;width:12px;height:12px;padding:0;border-top:1px solid #aaa;border-right:1px solid #aaa;-webkit-transform:rotate(45deg)}.course-navigator .expenselist-area{text-align:center}.course-navigator .expenselist-area .expense-course{position:relative;text-align:left}.course-navigator .expenselist-area .expense-course-title{font-size:.7rem;line-height:1rem;color:#000}.course-navigator .expenselist-area .expense-course-price{font-size:.75rem;line-height:1rem;color:#ff5b45}.course-navigator .expenselist-area .expense-course-promotion{display:none}.course-navigator .expenselist-area .expense-course-max{width:17.35rem;margin:.25rem auto;text-align:center}.course-navigator .expenselist-area .expense-course-max .expense-course-img{width:17.35rem;height:7.5rem}.course-navigator .expenselist-area .expense-course-max .expense-course-promotion{display:block;position:absolute;width:100%;bottom:2.25rem;line-height:1.5rem;font-size:.65rem;color:#fff;background:#000;opacity:.3}.course-navigator .expenselist-area .expense-course-mini-left{display:inline-block;width:8.5rem;margin:.5rem .3rem .5rem .5rem}.course-navigator .expenselist-area .expense-course-mini-left .expense-course-img{width:8.5rem;height:4.5rem}.course-navigator .expenselist-area .expense-course-mini-right{display:inline-block;width:8.5rem;margin:.5rem .5rem .5rem .3rem}.course-navigator .expenselist-area .expense-course-mini-right .expense-course-img{